.dropdown-menu.lang-dropdown {
  min-width: auto;
  width: auto;
}

.dropdown-item.active {
  background-color: #212529;
  text-shadow: 0 0 10px #ff3333, 0 0 20px #ff3333, 0 0 30px #ff0000;
}

a:active {
   background-color: #e60000;
}

.mobile-lang-menu {
    left: 50% !important;
    transform: translateX(-50%) !important;
}

/* --- EFFETTO GLOW ROSSO --- */
.nav-link.btn {
  color: #fff;
  transition: all 0.2s ease-in-out;
  text-shadow: none;
}

/* Effetto hover */
.nav-link.btn:hover {
  color: #fff;
  text-shadow: 0 0 10px #ff3333, 0 0 20px #ff3333, 0 0 30px #ff0000;
}

/* Effetto "active" — stesso effetto dell'hover */
.nav-link.btn.active,
.nav-link.btn.router-link-active {
  color: #fff !important;
  text-shadow: 0 0 10px #ff3333, 0 0 20px #ff3333, 0 0 30px #ff0000 !important;
}

.offcanvas {
  background-color: #0d0f11;
}